import React from 'react';
import '../App.css';
import { Button } from './Button';
import './Hero.css';

function Hero() {
  return (
    <div className='hero-container'>
      <video src='/videos/tomato.mp4' autoPlay loop muted />
      <h2>Tomato Jos</h2>
      <p>With Love From Farm to Kitchen</p>
      <div className='hero-btns'>
        <Button
          className='btns'
          buttonStyle='btn--outline'
          buttonSize='btn--large'
        >
          LEARN MORE
        </Button>
      </div>
    </div>
  );
}

export default Hero;
