import React from 'react'
import CardItem from './CardItem';
import './Cards.css';
import AOS from 'aos';
import 'aos/dist/aos.css';


AOS.init();


function Cards() {
  return (
    <div className='cards'>
        <h1>Check out these EPIC Destinations</h1>
        <div className='cards__container'>
            <div className='cards__wrapper'>
            <ul className='cards__items'>
            <CardItem
              src='images/t-01.jpg'
              text='Locally grown fresh Tomato just for you'
              label='Features'
              path='/about-us'
              animate='fade-right'
            />
            <CardItem
              src='images/t-02.jpg'
              text='Handpicked Harvested Tomato for your taste'
              label='Features'
              path='/about-us'
              animate='fade-left'
            />
          </ul>
          <h1>Some Recipes for You</h1>
          
          <ul className='cards__items'>
            <CardItem
              src='images/stew.png'
              text='Tomato Stew'
              label='Recipes'
              path='/products'
              animate='fade-up-right'
            />
            <CardItem
              src='images/spah.jpg'
              text='Pasta'
              label='Recipes'
              path='/products'
              animate='zoom-in'
            />
            <CardItem
              src='images/jollof.jpg'
              text='Jollof rice'
              label='Recipes'
              path='/products'
              animate='fade-up-left'
            />
          </ul>
            </div>
        </div>
    </div>
  )
}

export default Cards