import React from 'react'
import './Banner.css';
import './Cards.css';
import AOS from 'aos';
import 'aos/dist/aos.css';


AOS.init();



function Banner() {
  return (
      <div className='banner-container'>
        <div className='banner'>
            <h2 className='big' data-aos="flip-up">Prepared for your taste</h2>
        </div>
      </div>
    
  )
}

export default Banner